import Vue from 'vue'
import VueRouter from 'vue-router'
import Advertisement from '@/views/Advertisement.vue'
import RefineProduct from '@/views/RefineProduct.vue'
import ModelOutfit from '@/views/ModelOutfit.vue'
import Convert2D from '@/views/Convert2D.vue'
import LineArt from '@/views/LineArt.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    redirect: '/advertisement'
  },
  {
    path: '/advertisement',
    name: 'advertisement',
    component: Advertisement
    // component: () => import(/* webpackChunkName: "advertisement" */ '@/views/Advertisement.vue')
  },
  {
    path: '/refine-product',
    name: 'refineProduct',
    component: RefineProduct
    // component: () => import(/* webpackChunkName: "refineProduct" */ '@/views/RefineProduct.vue')
  },
  {
    path: '/model-outfit',
    name: 'Convert2D',
    component: ModelOutfit
    // component: () => import(/* webpackChunkName: "modelOutfit" */ '@/views/ModelOutfit.vue')
  },
  {
    path: '/convert-2d',
    name: 'convert2D',
    component: Convert2D
    // component: () => import(/* webpackChunkName: "convert2D" */ '@/views/Convert2D.vue')
  },
  {
    path: '/line-art',
    name: 'lineArt',
    component: LineArt
    // component: () => import(/* webpackChunkName: "LineArt" */ '@/views/LineArt.vue')
  }
]

const router = new VueRouter({
  routes
})

export default router
