<template>
  <el-dialog
    title="AI User Rules"
    :visible.sync="compVisible"
    width="60%"
    center
  >
    <span>XXX</span>

    <template #footer>
      <el-button @click="handleDialogCancel">Cancel</el-button>
      <el-button type="primary" @click="handleDialogAgree">Agree</el-button>
    </template>
  </el-dialog>
</template>

<script>
export default {
  model: {
    prop: 'visible',
    event: 'change'
  },

  props: {
    visible: {
      type: Boolean,
      required: true
    }
  },

  computed: {
    compVisible: {
      set (val) {
        this.$emit('change', val)
      },
      get () {
        return this.visible
      }
    }
  },

  methods: {
    handleDialogCancel () {
      this.compVisible = false
    },

    handleDialogAgree () {
      this.$emit('agree', true)
      this.compVisible = false
    }
  }
}
</script>
