<template>
  <el-upload
    class="image-uploader"
    action=""
    :style="uploadStyle"
    :show-file-list="false"
    :before-upload="beforeImgageUpload"
    :auto-upload="false"
    :limit="1"
  >
    <img
      v-if="imageUrl"
      :src="imageUrl"
      class="image"
      :style="imagePreviewStyle"
    />
    <i
      v-else
      class="el-icon-plus image-uploader-icon"
      :style="imagePreviewStyle"
    ></i>
  </el-upload>
</template>

<script>
export default {
  props: {
    uploadPosition: String,
    imagePreviewSize: String
  },

  data () {
    return {
      imageUrl: ''
    }
  },

  computed: {
    uploadStyle () {
      return {
        textAlign: this.uploadPosition
      }
    },

    imagePreviewStyle () {
      return {
        width: this.imagePreviewSize,
        height: this.imagePreviewSize,
        lineHeight: this.imagePreviewSize
      }
    }
  },

  methods: {
    beforeImgageUpload (file) {
      const isJPG = [
        'image/png',
        'image/jpg',
        'image/jpeg',
        'image/webp'
      ].includes(file.type)

      const isLt2M = file.size / 1024 / 1024 < 10

      if (!isJPG) {
        this.$message.error(
          'The uploaded image must be in the format of png/jpg/jpeg/webp.'
        )
      }

      if (!isLt2M) {
        this.$message.error(
          'The size of the uploaded image cannot exceed 10MB.'
        )
      }

      const isSize = new Promise((resolve, reject) => {
        const img = new Image()
        img.onload = () => {
          const width = img.width
          const height = img.height
          if (
            width >= 256 &&
            width <= 4096 &&
            height >= 256 &&
            height <= 4096
          ) {
            this.imageUrl = img.src
            this.$emit('change', file.raw)
            resolve()
          } else {
            this.$message.error(
              'The dimensions of the uploaded image must be between 256 and 4096 pixels in both length and width.'
            )
            reject(new Error())
          }
        }
        img.src = URL.createObjectURL(file.raw)
      })
      return isJPG && isLt2M && isSize
    }
  }
}
</script>

<style scoped lang="less">
@import "@/styles/variable.less";

.image-uploader {
  ::v-deep .el-upload {
    border: 2px dashed @border-color-base;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  ::v-deep .el-upload:hover {
    border-color: @color-primary;

    .image-uploader-icon {
      color: @color-primary;
    }
  }
}

.image-uploader-icon {
  font-size: 28px;
  color: @color-text-regular;
  width: @image-size;
  height: @image-size;
  line-height: @image-size;
  text-align: center;
}

.image {
  width: @image-size;
  height: @image-size;
  display: block;
}
</style>
